.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #f9f9f9;
  text-transform: uppercase;
  line-height: 22px;
  overflow: auto;
  z-index: 2;

  &__top {
    margin-bottom: 32px;

    @include on-tablet {
      margin-bottom: 56px;
    }
  }

  &__nav {
    margin-bottom: 48px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;
    color: inherit;
    letter-spacing: 2px;
    text-decoration: none;
  }

  &__call-to-order {
    display: block;
    width: fit-content;
    padding-bottom: 4px;
    border-bottom: 1px solid $main-text-color;
    color: inherit;
    letter-spacing: 2px;
    text-decoration: none;
  }
}
